.header {
  color: #fff;
  background-color: #2c2c2e;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
  padding: 10px 5%;
  display: flex;
}

.header .logo {
  background-color: #555;
  border-radius: 5px;
  padding: 10px;
  font-size: 2rem;
}

.header .nav {
  gap: 20px;
  font-family: Open Sans, serif;
  display: flex;
}

.header .nav .nav-link {
  color: #fff;
  font-size: .9rem;
  text-decoration: none;
}

.header .nav .nav-link:hover {
  color: #ccc;
}

@media (width >= 2560px) {
  .header {
