  }

  .header .logo {
    font-size: 3rem;
  }

  .header .nav {
    gap: 100px;
  }

  .header .nav .nav-link {
    font-size: 2rem;
  }
}

@media (width >= 1920px) and (width <= 2559px) {
  .header {
    padding: 10px 8%;
  }

