*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

button, input, select, textarea {
  background: none;
  border: none;
  outline: none;
}

body {
  font-family: sans-serif;
}

ul, ol {
  list-style: none;
}

img, video, audio {
  max-width: 100%;
  height: auto;
}

table {
  border-collapse: collapse;
  width: 100%;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

.footer {
  z-index: 2;
  background-color: #e6e6e6;
  border-top: 1px solid #000;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

.footer .top-section {
  border-bottom: 2px solid #000;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1000px;
  margin-bottom: 20px;
  padding-bottom: 30px;
  display: flex;
}

.footer .top-section .logo {
  color: #fff;
  background-color: #666;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  font-size: 2rem;
  font-weight: bold;
  display: flex;
}

.footer .top-section .social-icons {
  gap: 15px;
  display: flex;
}

.footer .top-section .social-icons a {
  font-size: 2rem;
  transition: transform .3s;
}

.footer .top-section .social-icons a:hover {
  transform: scale(1.2);
}

.footer .info {
  text-align: left;
  justify-content: center;
  width: 100%;
  max-width: 800px;
  display: flex;
}

.footer .info section {
  flex: 1;
  margin: 0 10px;
}

.footer .info section h3 {
  margin-bottom: 10px;
  font-family: Bebas Neue, serif;
  font-size: 1.1rem;
  font-weight: bold;
}

.footer .info section p {
  color: #333;
  margin-top: 5px;
  font-family: Open Sans, serif;
  font-size: .9rem;
  line-height: 1.6;
}

@media (width <= 767px) {
  .footer .info {
    text-align: center;
    flex-direction: column;
    align-items: center;
  }

  .footer .info section {
    margin-bottom: 20px;
  }

