body {
  flex-direction: column;
  height: 100%;
  display: flex;
}

body:before, body:after {
  content: "";
  filter: blur(4px);
  z-index: -1;
  background-image: url("FondoMenu7580.7b169f2f.avif");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

body:after {
  background-color: #0000004d;
}

main {
  flex: 1;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  display: flex;
}

.container {
  box-sizing: border-box;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  gap: 50px;
  width: 60vw;
  height: 90vh;
  max-height: 700px;
  margin-bottom: 150px;
  padding: 40px;
  display: grid;
}

.column {
  background-color: #fffc;
  border-radius: 15px;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  padding: 30px;
  display: flex;
  box-shadow: 0 0 20px #0003;
}

.cabezera {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding-left: 15px;
  display: flex;
  position: relative;
}

.cabezera i {
  margin-right: 15px;
  font-size: 24px;
}

.textoCabezera {
  color: #000;
  width: max-content;
  font-size: 18px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

table {
  border-collapse: collapse;
  flex-grow: 1;
  width: 100%;
}

td {
  text-align: center;
  border: 1px solid #ddd;
  padding: 15px;
}

@media (width <= 1199px) {
  .container {
    grid-template-columns: repeat(2, 1fr);
    width: 100vw;
  }
}

@media (width <= 700px) {
  .container {
    flex-direction: column;
    grid-template-rows: repeat(4, auto);
    grid-template-columns: 1fr;
    gap: 20px;
    width: 90vw;
    height: 80vh;
    margin-top: 0;
    padding: 20px;
    display: flex;
    overflow-y: auto;
  }

  .column {
    height: auto;
    padding: 20px;
  }
}
